

export const LI = (props) => {
    console.log(props)
    if(+props.text === +props.active){
        return (
            <li className="page-item active" onClick={()=>{
                props.fn(props.text);
            }} ><a className="page-link" href="#">{props.text}</a></li>
        )
    } else {
        return (
            <li className="page-item" onClick={()=>{
                props.fn(props.text);
            }} ><a className="page-link" href="#">{props.text}</a></li>
        )
    }
    
}