import './App.css';
import { Pagination } from './Pagination';
import React, { Component } from 'react';
import { User } from "./users";




class App extends Component {
  constructor(props) {
    super(props);

    this.state = {
      data: [],
      users: [],
      pagesObj : {},
      paginationItem : [],
      pagignationKey : 0,
      page : 0,
    };
  }

  changePage = (val) => {
    if(val === 'Previous' || val === 'Next'){
      this.changePagination(val);
    } else {
      this.setState({
        users : this.state.data[val-1],
        page : +val
      })
    }
    
  }

  changePagination = (val) => {
    let k = this.state.pagignationKey+1
    if(val === 'Previous') {
      if(k > 2){
          
          let key = this.state.pagignationKey-1
          this.setState({
            paginationItem : this.state.pagesObj[''+key],
            pagignationKey : key 
          })
        this.changePage(+(this.state.paginationItem[0])-1)
      }
    } else {
      if(k < 8) {
        console.log(k)
        let key = this.state.pagignationKey+1
        this.setState({
          paginationItem : this.state.pagesObj[''+key],
          pagignationKey : key
        })
        this.changePage(+(this.state.paginationItem[this.state.paginationItem.length-1])+1)
      }
      }    
}
    

  

  componentDidMount() {
    fetch('https://raw.githubusercontent.com/Rajavasanthan/jsondata/master/pagenation.json').then(
      (val) => val.json()).then((val) => {
        let userArray = [];
        for(let i=0; i<val.length; i+=3){
          let arr = [];
          arr.push(val[i], val[i+1], val[i+2]);
          userArray.push(arr.filter(i=>i!==undefined));
        }
        let pgsObj = {};
        let count = 0;
        for(let i=1; i<=userArray.length; i+=5) {
          count++;
          let pgs = [];
          pgs.push(i, i+1, i+2, i+3, i+4);
          pgsObj[count] = pgs.filter(p=>p<=userArray.length)
        }
        
        this.setState({
          data: userArray,
          users :userArray[0],
          pagesObj : pgsObj,
          paginationItem : pgsObj['1'],
          pagignationKey : 1,
          page : 1
        })
      })
  }

  render() {
    const users = this.state.users;
    if(users !== null){
      return (
        <div className="App">
          <div className="container">
            <div className="row mt-5 justify-content-center">
              {users.map((ele, index)=>{
                return(<User key={index} value={ele}/>)
              })}
            </div>
            <div className="row mt-5 justify-content-center">
              <Pagination items={this.state.paginationItem} change={this.changePage} page={this.state.page}/>
            </div>
          </div>
        </div>
      )
    } 
      return(
        <div className="App">
          <div className="container">
            <div className="row mt-5 justify-content-center">
            </div></div></div>
      )
    
  }


}

export default App;
