
export const User = (props) => {
    return(
        <div className="card m-2 col-sm-12">
            <div className="card-body">
                <div className="row">
                    <div className="col-sm-2">
                        <p className="display-2">
                            {(+props.value.id)}
                        </p>
                    </div>
                    <div className="col-sm-10 mt-4">
                        <p className="h3">
                        {props.value.name}
                        </p>
                        <p className="h4">
                        {props.value.email}
                        </p>
                    </div>
                </div>
            </div>
        </div>
    )
}