import {LI} from "./ListItem";

export const Pagination = (props) => {
    console.log(props.page);
    return (
        <nav aria-label="Page navigation example">
            <ul className="pagination">
               <LI text='Previous' fn={props.change}/>
               {props.items.map(i=>{
                   return (<LI text={i} key={i} fn={props.change} active={props.page}/>)
               })}
               <LI text='Next' fn={props.change}/> 
            </ul>
        </nav>
    )
}